/*====================== Button Style ====================*/
button {
    border: none;
    transition: .2s linear;

    &:focus {
        outline: none;
        box-shadow: none;
    }
}

// Custom Button
.btn {
    &:focus {
        outline: none;
        box-shadow: none;
    }

    color: hsl(var(--white));
    font-weight: 500;
    padding: 11px 20px;
    border-radius: 4px;
    position: relative;
    z-index: 1;
    text-align: center;
    text-transform: capitalize;
    font-weight: 500;
    transition: .5s;
    font-family: var(--heading-font);
    font-size: 16px;
    transition: .5s;
    overflow: hidden;

    @include slg-screen {
        padding: 10px 18px;
    }

    @include sm-screen {
        padding: 11px 25px;
    }

    @include msm-screen {
        padding: 6px 7px;
    }

    &:hover,
    &:focus {
        color: hsl(var(--white));
        border-color: transparent;
        border-radius: 4px;
    }

    &--lg {
        padding: 15px 35px;

        @include sm-screen {
            padding: 13px 30px;
        }

        @include msm-screen {
            padding: 11px 25px;
        }
    }

    &--sm {
        padding: 4px 12px;
        background-color: hsl(var(--base));
        color: hsl(var(--white));
        font-size: 14px;

        &:hover,
        &:focus {
            background-color: hsl(var(--white));
            border: 1px solid hsl(var(--base));
            color: hsl(var(--base)) !important;

        }

        @include sm-screen {
            padding: 5px 8px;
        }
    }

    &--ssm {
        padding: 2px 10px;
        background-color: hsl(var(--base));
        color: hsl(var(--white));
        font-size: 12px;

        &:hover,
        &:focus {
            background-color: hsl(var(--white));
            border: 1px solid hsl(var(--base));
            color: hsl(var(--base));
        }

        @include sm-screen {
            padding: 3px 8px;
        }
    }

    &--icon {
        width: 35px;
        height: 35px;
        line-height: 35px;
        font-size: 15px;
        padding: 0;
    }

    // ============= Different Color Button Start ==================//
    &--base {
        background: hsl(var(--base));
        background-size: cover;
        background-repeat: no-repeat;
        color: hsl(var(--white));
        border-radius: 4px;

        &:hover,
        &:focus {
            background: hsl(var(--white));
            box-shadow: 0px 4px 8px 0px hsl(var(--base));
            color: hsl(var(--base));

        }

        &.pill {
            border-radius: 35px;
        }

        &.circle {
            border-radius: 50%;
            width: 53px;
            height: 53px;
            color: hsl(var(--black));


            i {
                font-size: 22px;
                rotate: -30deg;
                transition: .5s;
            }

            &:hover {
                i {
                    rotate: 30deg;
                    transition: .5s;
                }
            }
        }

        &.outline {
            background: transparent;
            background-size: cover;
            background-repeat: no-repeat;
            border: none !important;
            color: hsl(var(--base));
            border-radius: 4px;
            border: 1px solid hsl(var(--white)/0.09) !important;

            &.hover-white-c {

