// button circle
.btn-circle {
    border-radius: 50%;
    width: 139px;
    height: 139px;
    background: hsl(var(--base));
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    color: hsl(var(--white));
    rotate: -45deg;
    position: relative;
    border: 8px solid hsl(var(--white));

    i {
        transition: all 0.3s ease-out;
    }


    &::after {
        content: "";
        width: 100px;
        height: 100px;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        border: 1px dashed hsl(var(--white));
        transform: translate(-50%, -50%);
    }

    &:hover {
        color: hsl(var(--white));

        i {
            rotate: 90deg;
            transition: all 0.3s ease-out;
        }

    }
}

/*========= Very small btn ======== */
.btn--small {
    color: hsl(var(--base));
    font-weight: 700;
    padding: 3px 10px;
    border-radius: 4px;
    position: relative;
    z-index: 1;
    text-align: center;
    border: 1px solid hsl(var(--base));
    text-transform: capitalize;
    font-weight: 600;
    transition: 0.5s;
    font-family: var(--heading-font);
    font-size: 13px;
    transition: 0.5s;

    &:hover,
    .btn--small:focus {
        background-color: hsl(var(--base));
        border: 1px solid hsl(var(--base));
        color: hsl(var(--white));
        border-radius: 6px;
    }
}




.copytext {
    border-radius: 0px 6px 6px 0px !important;
}



@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}


